<template>
  <div class="boardContent">
    <!-- <div v-for="(item, index) in ticketContentMessage" :key="index">
      {{ index }}: {{ item }}
    </div> -->
    <table style="color: var(--color-pure-black);border-collapse:collapse;border:none;width:100%">
        <tr style="border-top:.5px solid #CCC; border-left:.5px solid #CCC;border-right:.5px solid #CCC;">
          <th style="padding:6px 30px;font-size: 30px;background-color: #EEE;" colspan="2">详情</th>
        </tr>
        <tr v-for="(value, key) in ticketContent" :key="key">
          <td style="padding: 6px 10px;font-weight: bold;border: .5px solid #CCC;width:125px;">{{processKey(key)}}</td>
          <td style="padding: 3px 10px;font-weight: lighter;border: .5px solid #CCC;">

            <p v-if="key!=='video_short'">{{processValue(key, value)}}</p>
            <p v-if="key==='food'">{{ ticketContent.food.description }}</p>
            
            <p v-if="key==='video_short'">
            <h1>{{ values }}</h1>
              <video :src="videoUrl" controls="controls" style="width:300px;"></video>
            </p>
            <div v-if="key==='gain'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='product_general_features'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='dining'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='service'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='slogan1'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='slogan2'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='facility'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='categories'">
              <div style="display:flex;">
                <p v-for="(item, index) in value" :key="index" style="margin-right:20px;">
                {{categoryMap.find(category => category.id == item).name }}
                </p>
              </div>
            </div>
            <div v-if="key==='features'">
              <div style="display:flex;">
                <p v-for="(item, index) in value" :key="index" style="margin-right:20px;">{{ featuresMap[item['feature']] }}</p>
              </div>
            </div>
            <div v-if="key==='foundation'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='healthcare'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='accommodation'||key==='aboutAccommodation'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='communication'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='invoice'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='advantage1_intro'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='advantage2_intro'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='insurance'">
              <p v-for="(item, index) in value" :key="index">
                {{item.type}}: {{item.name}}-保额{{item.insured_amount}}
              </p>
            </div>
            <div v-if="key==='staffing'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='horizontal_shows'">
              <div style="display:flex;">
                <img v-for="(item, index) in value"
                  :key="index" :src="item.url||item"
                  style="margin-right:20px;width:100px;"
                  @load="imageLoad" />
              </div>
            </div>
            <div v-if="key==='vertical_shows'">
              <div style="display:flex;">
                <img v-for="(item, index) in value"
                  :key="index" :src="item.url||item"
                  style="margin-right:20px;height:100px;"
                  @load="imageLoad" />
              </div>
            </div>
            <div v-if="key==='price_description'">
              <p v-for="(item, index) in value" :key="index">{{item}}</p>
            </div>
            <div v-if="key==='prepare_instruction'">
              <div v-for="(item, index) in value" :key="index">
                <p style="font-weight:bold;">{{item.name}}</p>
                <div>
                  <p v-for="(item2, index) in item.content" :key="index">{{item2}}</p>
                </div>
              </div>
            </div>
            <div v-if="key==='syllabus'">
              <div v-for="(item, index) in value" :key="index">
                <p style="font-weight:bold;">{{item.title}}</p>
                <div>
                  <p v-for="(item2, index) in item.details" :key="index">{{item2}}</p>
                </div>
              </div>
            </div>
            <div v-if="key==='schedule'">
              <div v-for="(item, index) in value" :key="index">
                <p style="font-weight:bold;">{{item.date}}</p>
                <div>
                  <div style="display:flex;" v-for="(item2, index) in item.content" :key="index">
                    <p style="min-width:179px;">{{item2.time}}</p>&nbsp;&nbsp;&nbsp;
                    <p>{{item2.detail}}</p>
                  </div>
                </div>
              </div>
            </div>
          </td>
        </tr>
      </table>
    <div v-show="ticketContent.horizontal_shows?.length != 0">活动头版图:</div>
    <img
      @click="watchBigImg(item)"
      :src="'https://www.camptogo.com' + item.url"
      :key="index"
      v-for="(item, index) in ticketContent.horizontal_shows"
      alt="" />
    <div v-show="ticketContent.vertical_shows?.length != 0">竖版风采图:</div>
    <img
      @click="watchBigImg(item)"
      :src="'https://www.camptogo.com' + item.url"
      :key="index"
      v-for="(item, index) in ticketContent.vertical_shows"
      alt="" />
    <div v-show="ticketContent.video_short?.url">
      <div>短视频</div>
      <video width="320" height="240" controls="controls">
        <source
          :src="'https://www.camptogo.com' + ticketContent.video_short?.url"
          type="video/mp4" />
      </video>
    </div>
  </div>
</template>

<script>

export default {

  name: 'ProductTicketContentXXX',
  props: {
    ticketContent: {
      type: [Object, Array],
      default() {
        return []
      }
    }
  },
  watch: {
    ticketContent: function (newDa, oldDa) {
      console.log(newDa)
      console.log(JSON.stringify(newDa))
    }
  },
  created() {
    console.log(this.ticketContent)
  },
  methods: {
    watchBigImg(url) {
      window.open('https://www.camptogo.com' + url.url, '_blank')
    },
    imageLoad() {
      this.$bus.$emit('itemImageLoad');
    },

    processKey(keyName) {
      switch(keyName) {
          case 'full_name':
            return '产品全称';
          case 'short_name':
            return '产品简称';
          case 'exclusive':
            return '是否独家';
          case 'free':
            return '是否免费';
          case 'welfare':
            return '是否是公益产品';
          case 'nonprofit':
            return '是否非盈利产品';
          case 'invoice':
            return '服务商向消费者开具的发票类型';
          case 'features':
            return '特色'
          case 'insurance':
            return '保险类型';
          case 'expenses_rate':
            return '服务费率';
          case 'other_expenses':
            return '其他费用';
          case 'oversold_limit':
            return '可超卖人数';
          case 'size_valid':
            return '成团人数';
          case 'recruit_total':
            return '总人数';
          case 'staffing_ratio':
            return '工作人员比例';
          case 'categories':
            return '活动类别';
          case 'product_general_features':
            return '活动特色';
          case 'age_min':
            return '最小招生年龄';
          case 'age_max':
            return '最大招生年龄';
          case 'activitymode_id':
            return '日程形式';
          case 'theme_related':
            return '内容形式';
          case 'difficulty':
            return '活动难易度';
          case 'foundation':
            return '基础要求';
          case 'activity_province':
            return '活动省份';
          case 'date_created':
            return '创建时间'
          case 'theme':
            return '主题'
          case 'location_province':
            return '活动省份';
          case 'activity_city':
            return '活动城市';
          case 'location_city':
            return '活动城市';
          case 'activity_district':
            return '活动区县';
          case 'liability_insurance_self_if':
            return '是否自己购买责任险'
          case 'accident_insurance_self_if':
            return '是否自己购买意外险'
          case 'location_district':
            return '活动区县';
          case 'liability_insurance_self_details':
            return '购买的责任险'
          case 'accident_insurance_self_details':
            return '购买的意外险'
          case 'price_exclude':
            return '价格不包含'
          case 'price_include':
            return '价格包含'
            case 'food':
            return '食物'
          case 'activity_detail':
            return '活动详细地点';
          case 'location_street':
            return '活动详细地点';
          case 'activity_start_time':
            return '活动开始时间';
          case 'activity_end_time':
            return '活动结束时间';
          case 'activity_days':
            return '活动天数';
          case 'product_launching_time':
            return '预计活动上架时间';
          case 'product_expiration_time':
            return '活动截止报名时间';
          case 'promotion':
            return '是否优惠';
          case 'price_original':
            return '原价';
          case 'price_selling':
            return '现价';
          case 'price_description':
            return '价格说明';
          case 'refundpolicy_id':
            return '退款政策';
          case 'dining':
            return '餐饮标准';
          case 'accommodation':
            return '住宿管理';
          case 'aboutAccommodation':
            return '住宿管理';
          case 'communication':
            return '信息交互';
          case 'healthcare':
            return '医疗保障';
          case 'horizontal_shows':
            return '横板图片';
          case 'vertical_shows':
            return '竖版图片';
          case 'video_short':
            return '宣传视频';
          case 'prepare_instruction':
            return '开营须知';
          case 'slogan1':
            return '主宣传语';
          case 'topic':
            return '产品主题';
          case 'topic_intro':
            return '主题简介';
          case 'slogan2':
            return '副宣传语';
          case 'advantage1':
            return '产品优势1';
          case 'advantage1_intro':
            return '产品优势1简介';
          case 'advantage2':
            return '产品优势2';
          case 'advantage2_intro':
            return '产品优势2简介';
          case 'gain':
            return '收获内容';
          case 'facility':
            return '硬件设施';
          case 'service':
            return '服务';
          case 'staffing':
            return '人员配置';
          case 'syllabus':
            return '教学提纲';
          case 'schedule':
            return '日程表';
          case 'provider_id':
            return '服务商id';
          case 'id':
            return '服务商id';
          case 'language':
            return '语言'
          default:
            return keyName;
        }
    },
    processValue(key, value) {
      const typeStr = Object.prototype.toString.apply(value);
      // 价格去除小数点
      if(key==='price_original' || key==='price_selling') {
        return `${value}`.slice(0,-2)
      }
      if(key==='daily_schedule') {
        console.log('📕','日程安排')
      }
      if(typeStr === '[object String]') {
        if(value) return value;
        else return null;
      }
     
      else if(typeStr === '[object Boolean]') {
        if(value) {
          return '是';
        }
        else {
          return '否';
        }
      }
      else if(typeStr === '[object Number]') {
        if(key === 'activitymode_id') {
          return this.activityModesGOT.find(item => item.id === value).name;
        }
        else {
          return value;
        }
      }
      else {
        return null;
      }
    },
    findNameByID(id, array) {
      if(Object.prototype.toString.apply(array) === '[object Array]') {
        return array.find(item => item.id === id).name;
      }
      else {
        return '【无数据】'
      }
    }
  },
  data() {
    return {
      imgList: ['vertical_shows', 'horizontal_shows', 'video_short']   ,
      featuresMap : {
  1:'国际奖项',
  2:'国家奖项',
  3:'省市奖项',
  4:'高级职称',
  5:'中级职称',
  6:'荣誉称号',
  7:'空降嘉宾',
  8:'独家授权',
  9:'教学5年+',
  10:'教学10年+',
  11:'教学20年+',
  12:'教学30年+',
  13:'教学40年+',
  14:'专业认证',
  15:'联合教学',
  16:'自营场地',
  17:'国际品牌',
  18:'知名品牌',
  19:'前沿领域',
  20:'自研课程',
  21:'小班授课',
  22:'可用优惠',
  23:'高级住宿',
  24:'医疗保障',
  25:'超优场地',
  26:'著有作品'
},
      categoryMap:[
      {
        name: "体育",
        id: 1,
        selected: false,
      },
      {
        name: "机械",
        id: 3,
        selected: false,
      },
      {
        name: "手工",
        id: 4,
        selected: false,
      },
      {
        name: "地理",
        id: 5,
        selected: false,
      },
      {
        name: "医疗",
        id: 6,
        selected: false,
      },
      {
        name: "军事",
        id: 7,
        selected: false,
      },
      {
        name: "天文",
        id: 8,
        selected: false,
      },
      {
        name: "自然",
        id: 9,
        selected: false,
      },
      {
        name: "户外",
        id: 10,
        selected: false,
      },
      {
        name: "艺术",
        id: 11,
        selected: false,
      },
      {
        name: "人文",
        id: 12,
        selected: false,
      },
      {
        name: "计算机",
        id: 13,
        selected: false,
      },
      {
        name: "商业",
        id: 14,
        selected: false,
      },
      {
        name: "其他",
        id: 15,
        selected: false,
      },
      {
        name: "亲子",
        id: 17,
        selected: false,
      },
    ]
    }
  },
  computed: {
    videoUrl(){
      const videoStr= this.$props.ticketContent.video_short
      if(typeof videoStr ==='string') return videoStr.slice(9,videoStr.length-2)
      else if(videoStr.url) {
        return videoStr.url
      }
      else {
        console.log('error_url',videoStr)
        return videoStr
      }
    },
    ticketContentMessage() {
      let obj = {}
      for (let item in this.ticketContent) {
        if (this.imgList.includes(item)) {
          continue
        }
        obj[item] = this.ticketContent[item]
      }
      return obj
    }
  }
}
</script>

<style scoped>
div {
  user-select: all !important;
}
img {
  width: 300px;
  height: 200px;
}
</style>
